import Vue from "vue";
import Router from "vue-router";

import routers from "@/router-map.js";

Vue.use(Router);


var routers_extend = [{
  path: "/",
  redirect: {
    name: "home"
  }
}];

export default new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: routers.concat(routers_extend),
  scrollBehavior: function (to, from, savedPosition) {
    if (to.hash) {
      return {
        selector: to.hash
      };
    }
    if (savedPosition) {
      return savedPosition;
    } else {
      return {
        x: 0,
        y: 0
      };
    }
  }
});